#Slider{
    margin-top: 200px;
    padding-top: 58px;
    display: flex;
    position: relative;
    .line{
        height: 207px;
        width: 776px;
        position: absolute;
        z-index: 1;
        left: -357px;
        top: 151px;
        background-color: #FAF4EF;
        z-index: 1;
    }
    .flat{
        position: relative;
        z-index: 2;
        // float: left;
    }
    .legend{
        flex-grow: 1;
        padding-left: 68px;
        h2{
            margin: 70px 0 95px;
        }
        .subtitle{
            font-family: 'Roboto', sans-serif;
            font-size: 24px;
            font-weight: 400;
            line-height: 36px;
            color: #737373;
        }
        .buttons{
            display: flex;
            margin-top: 50px;
            .left{
                margin-right: 12px;
            }
            .left,
            .right{
                width: 70px;
                height: 70px;
                background: rgba(250, 244, 239, 0.5);
                display: flex;
                justify-content: center;
                align-items: center;
                cursor: pointer;
                transition: all 0.2s ease-in-out;
                &:hover{
                    transform: scale(0.9);
                }
            }
        }
    }
}

@media screen and (max-width: 390px){
    #Slider{
        display: block;
        margin-top: 100px;
        .line{
            width: 390px;
            height: 86px;
            left: 0px;
            top: 98px;
        }
        .flat{
            z-index: 2;
            img{
                float: right;
                width: 356px;
                height: 377px;
            }
        }
        .legend{
            padding-left: 34px;
            clear: both;
            h2{
                padding-top: 50px;
                margin: 0 0 50px 0;
            }
        }
    }
}