@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

@font-face {
    font-family: 'DM Serif Display Regular';
    font-style: normal;
    font-weight: normal;
    src: local('DM Serif Display Regular'), url('../public/DMSerifDisplay-Regular.woff') format('woff');
}
    

@font-face {
    font-family: 'DM Serif Display Italic';
    font-style: normal;
    font-weight: normal;
    src: local('DM Serif Display Italic'), url('../public/DMSerifDisplay-Italic.woff') format('woff');
}

body{
    color: #4B4B4B;
    line-height: 95.6%;
    max-width: 1440px;
    margin: 0 auto;
}

a{
    text-decoration: none;
    color: #4B4B4B;
    border-bottom: 3px solid transparent;
    &:hover{
        border-bottom: 3px solid #4B4B4B;
    }
}

h1{
    font-family: 'DM Serif Display';
    font-size: 64px;
    font-weight: 600;
    line-height: 64px;
}

h2{
    font-family: 'DM Serif Display';
    font-weight: 600;
    font-size: 64px;
    line-height: 100%;
}

.App{
    padding: 0 110px 0 103px;
}

@media screen and (max-width: 390px){
    .App{
        overflow: hidden;
        max-width: 390px;
        padding: 0;
    }
    h2{
        font-size: 30px;
        font-weight: 400;
        line-height: 30px;
        letter-spacing: 0em;
        text-align: left;
    }
}