#Header{
    padding: 45px 0 90px;
    display: flex;
    justify-content: space-between;
    .branding{
        font-family: 'DM Serif Display Regular';
        font-size: 36px;
        padding-top: 27px;
    }
    nav{
        font-size: 24px;
        line-height: 95.6%;
        font-family: 'Archivo Narrow', sans-serif;
        ul{
            list-style: none;
            display: flex;
            li{
                margin-left: 60px;
                &:first-child{
                    margin: 0;
                }
            }
        }
    }
    .header-icons{
        display: flex;
        align-items: center;
        .cart{
            margin-left: 15px; 
            cursor: pointer;
        }
        .search{
            cursor: pointer;
        }
    }
    .burger{
        display: none;
    }
}

@media screen and (max-width: 390px){
    #Header{
        padding: 0 27px 0 27px;
        align-items: center;
        nav, 
        .header-icons{
            display: none;
        }
        .branding{
            padding: 35px 0;
        }
        .burger{
            display: block;
            width: 40px;
            height: 40px;
        }
    }
}