#Footer{
    background: #FAF4EF;
    border-radius: 0px 100px 0px 0px;
    height: 347px;
    width: 100%;
    margin: 160px 0;
    padding: 57px 0 0 88px;
    h2{
        font-size: 64px;
        font-weight: 500;
        line-height: 96px;
        margin-bottom: 20px;
    }
    .buttons{
        display: flex;
        div{
            width: 214px;
            height: 54px;
            font-family: 'Roboto', sans-serif;
            font-size: 24px;
            font-weight: 500;
            line-height: 36px;
            display: flex;
            justify-content: center;
            align-items: center;
        }
        .btn-secondary{
            color: #FFFFFF;
            background: #4B4B4B;
            margin-right: 30px;
        }
        .btn-primary{
            background: #FFFFFF;
            color: #4B4B4B;
        }
    }
}

@media screen and (max-width: 390px) {
    #Footer{
        width: 320px;
        height: 299px;
        margin: 50px auto;
        padding: 20px 0 20px 35px;
        h2{
            font-size: 30px;
            font-weight: 400;
            line-height: 45px;
            letter-spacing: 0em;
            text-align: left;
            width: 60%;
        }
        .buttons{
            flex-direction: column;
            .btn-secondary{
                margin-bottom: 12px;
            }
        }
    }
}