#Jumbotron{
    position: relative;
    clear: both;
    .line{
        height: 207px;
        width: 776px;
        position: absolute;
        z-index: 1;
        top: 56px;
        right: -125px;
        background-color: #FAF4EF;
    }
    img{
        position: relative;
        z-index: 2;
        float: right;
        border-radius: 0px 0px 0px 75px;
    }
    .legend{
        position: absolute;
        width: 492px;
        height: 452px;
        left: 0;
        top: 254px;
        z-index: 3;
        background: rgba(255, 255, 255, 0.78);
        backdrop-filter: blur(8px);
        .subtitle{
            font-family: Roboto;
            font-size: 24px;
            font-weight: 400;
            line-height: 29px;
        }
    }
    &:after{
        content: '';
        width: 100%;
        height: 1px;
        clear: both;
    }
}

@media screen and (max-width: 390px){
    #Jumbotron{
        .line{
            display: none;
        }
        img{
            left: 98px;
            width: 537px;
            height: 365px;
            float: none;
        }
        .legend{
            width: 234px;
            height: 215px;
            top: 75px;
            left: 27px;
            h1{
                font-size: 30px;
                font-weight: 400;
                line-height: 30px;
                letter-spacing: 0em;
                text-align: left;
            }
            .subtitle{
                font-size: 12px;
                font-weight: 400;
                line-height: 14px;
                letter-spacing: 0em;
                text-align: left;
            }
        }
    }
}