#Products{
    clear: both;
    padding-top: 228px;
    nav.products-menu{
        text-align: center;
        font-family: 'Roboto', sans-serif;
        font-size: 24px;
        font-weight: 500;
        line-height: 24px;
        margin-bottom: 85px;
        ul{
            display: flex;
            justify-content: center;
            list-style: none;
            li{
                margin-left: 55px;
                color: #4B4B4B;
                opacity: .5;
                &.active{
                    opacity: 1;
                }
                &:first-child{
                    margin-left: 0;
                }
            }
        }
    }
    nav.products-menu-mobile{
        display: none;
    }
    .catalog{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(2, 1fr);
        grid-column-gap: 67px;
        grid-row-gap: 64px;
        .product{
            background: #FAF4EF;
            border-radius: 0px 0px 0px 50px;
            display: flex;
            flex-direction: column;
            width: 366px;
            height: 420px;
            position: relative;
            padding: 40px 0 0 48px;
            font-family: 'DM Serif Display Regular';
            .product-title{
                text-align: left;
                font-size: 36px;
                font-weight: 400;
                line-height: 36px;
            }
            .product-price{
                color: #737373;
                font-size: 24px;
                font-weight: 400;
                text-align: left;
                padding-top: 25px;
            }
            img{
                text-align: center;
                position: absolute;
                left: 50%;
                bottom: -10%;
                transform: translateX(-50%);
            }
        }
    }
}

@media screen and (max-width: 390px){
    #Products{
        padding: 86px 34px 0 34px;
        h2{
            display: inline-block;
            margin: 0;
        }
        nav.products-menu{
            display: none;
        }
        nav.products-menu-mobile{
            display: block;
            float: right;
            ul li{
                font-size: 20px;
                font-weight: 500;
                line-height: 20px;
                letter-spacing: 0em;
                text-align: left;                
            }
        }
        .catalog{
            display: grid;
            grid-template-columns: repeat(6, 1fr);
            grid-template-rows: repeat(1, 1fr);
            margin-top: 30px;
            .product{
                width: 286px;
                height: 363.69px;
            }
        }
    }
}